export default {
	data() {
		return {
			avatar: '../../../static/img/release.png',
			nickname: 'jay',
			task: {
				'credit': '',
				'dailyInviteTimes': 0,
				'dailyShare': '',
				'dailyWatchVideoTimes': 0,
				'dailyCheckIn': '',
				'totalCheckInTimes': 0,
				'totalDrawTimes': 1,
				'totalInviteTimes': 0,
				'totalWatchVideoTimes': 0,
				'weeklyInviteTimes': 0
			},
			loading: false
		}
	},
	onShow() {
		this.task = this.$store.state.task
	},
	methods: {
		checkIn() {
			if (this.loading) return
			this.loading = true
			this.doRequest('/task/update', {
				uuid: this.$store.state.uuid,
				type: 'checkIn',
				num: '+8'
			}).then(res=>{
				uni.showToast({
					title: '签到成功，积分 +8'
				})
				this.$store.commit('updateTask', {
					dailyCheckIn: true,
					totalCheckInTimes: 1,
					credit: 8
				})
				this.loading = false
			}).finally(_=>{
				this.loading = false
			})
		},
		watch() {
			this.doRequest('/task/update', {
				uuid: this.$store.state.uuid,
				type: 'watch',
				num: '+15'
			}).then(res=>{
				uni.showToast({
					title: '积分 +15'
				})
				this.$store.commit('updateTask', {
					dailyWatchVideoTimes: 1,
					totalWatchVideoTimes: 1,
					credit: 15
				})
			}).finally(()=>{
				this.loading = false
			})
		},
		share() {
			this.doRequest('/task/update', {
				uuid: this.$store.state.uuid,
				type: 'share',
				num: '+30'
			}).then(res=>{
				uni.showToast({
					title: '积分 +30'
				})
				this.task.dailyShare = true
				this.$store.commit('updateTask', {
					dailyShare: true,
					credit: 30
				})
				this.loading = false
			})
		},
		getUserInfo(){
			this.doRequest('/task/get', { uuid: this.$store.state.uuid })
			.then(res=>{
				this.task = res.data
				this.$store.commit('setTask', res.data)
				let pages = getCurrentPages();
				let page = pages[pages.length - 1];
				page.onLoad()
			}).catch(_=>{
				uni.showToast({
					title: '网络繁忙，请稍后再试',
					icon: 'none'
				})
			})
		},
		video() {
			if (this.loading) return
			this.loading = true
			let start, end;
			let videoAd = qq.createRewardedVideoAd({
			adUnitId: 'e2ef4a02df6b0d9a8ebd13beb5781199'
			})
			videoAd.onLoad(function(res){
			})
			videoAd.onError((res)=>{
				uni.showToast({
					title: '视频加载失败，请去个人中心反馈意见'
				})
			})
			
			videoAd.onClose((res)=>{
				end = new Date().getTime()
				if ((end-start)/1000 > 15) {
					this.watch()
				}
			})
			
			videoAd.load()
			.then(() => {
				videoAd.show().then(() => {
					start = new Date().getTime()
				})
				.catch(err => {
				})
			})
			.catch(err => {
			})
		  
		}
	},
	onPullDownRefresh() {
		this.getUserInfo();
	},
	onShareAppMessage(res) {
		if (this.loading) return
		this.loading = true
		let that = this
	    return {
	      title: '王者荣耀，免费抽皮肤，和好兄弟开黑...',
	      path: '/pages/tabbar/login/login?parentID='+ this.$store.state.uuid,
		  success: function(){
			  that.share()
		  }
	    }
	}
}
